@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Roboto&display=swap);
.App {
  text-align: center;
  min-height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-image: linear-gradient(
    -225deg,
    #77a1d3 0%,
    #79cbca 56%,
    #e684ae 100%
  );

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.nav-link {
  color: black !important;
}

.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
  background-color: #ffffff3a !important;
  border-bottom: 0px solid rgba(236, 236, 236, 0.473);
}
.nav-tabs {
  border-bottom: 0px !important;
}

a:hover {
  border-bottom: 2px solid rgba(224, 224, 224, 0.473);
}

:root {
  --main-color: rgb(182, 233, 253);
  --me-margin: 40px;
  --font-color: #373a3c;
}

.main {
  margin: 0px;
  background-image: linear-gradient(
    -225deg,
    #77a1d3 0%,
    #79cbca 56%,
    #e684ae 100%
  );
}
h1 {
  font-family: Bebas Neue, cursive;
  font-size: 50px !important;
  text-align: center;

  margin: 0px;
  letter-spacing: 3px;
}

h2 {
  margin: 10px 10px;
  text-align: center;
  font-family: Roboto;
  font-size: 25px !important;
}

.pages {
  margin: 2rem 2rem 2rem 2rem;
  padding: 1rem 1rem;
}

.project {
  width: 350px;
  height: 200px;
  object-fit: cover;
}

.study {
  margin: 5px;
}

.icons {
  display: flex;
  justify-content: center;
  margin: 15px;
}

.card {
  padding: 2px;
  text-align: center;
  line-height: 20px;
  transition: all 0.5s ease-in-out;
  box-shadow: 0 1px 4px rgb(0 0 0 / 40%);
}

.card-body {
  padding: 5px;
}

.card:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  transition: all 0.5s;
}

.projects {
  margin: 0rem 1.5rem;
}

.container {
  max-width: 1140px;
}

.mainBody {
  margin: 1rem 10rem;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
}

.card-title {
  font-weight: 400;
}

body {
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
  font-family: "Roboto", sans-serif;
  margin: 0px;
  text-align: center;
}

.img-thumbnail {
  width: 310px !important;
  height: 200px !important;
  object-fit: cover;
}

.gel {
  width: 250px;
  height: 250px;
  object-fit: cover;
}

.aboutme {
  font-size: 23px;
  line-height: 2rem;
  padding: 1rem;
}

.footer {
  padding: 1rem 1rem;
  background-image: linear-gradient(
    -225deg,
    #77a1d3 0%,
    #79cbca 56%,
    #e684ae 100%
  );
  display: flex;
  justify-content: center;
}

.social {
  display: flex;
  align-items: center;
  align-content: center;
  margin-left: 3rem;
  margin-right: 3rem;
}

.socialicon {
  width: 40px;
  height: 40px;
  object-fit: cover;
}

.icons {
  display: flex;
  justify-content: center;
  margin: 15px;
}

a {
  color: black !important;
}

.lead {
  display: flex;
  align-items: center;
}

